.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;           /* Allows nav to move onto a new line */
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
    margin-bottom: 1rem;
}

.logo {
    white-space: nowrap;
}

.menu-icon {
    display: none;
    font-size: 2em;
    cursor: pointer;
}

/* Desktop nav */
.nav {
    /* The default display on larger screens can remain flex if you want horizontal links */
}

.nav ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
}

.nav li {
    margin-left: 20px;
}

.nav a {
    text-decoration: none;
    color: #fff;
}

/* --- Mobile Styles --- */
@media (max-width: 768px) {
    .menu-icon {
        display: block;   /* Show hamburger on mobile */
        justify-content: center;
        align-items: center;
    }

    /* Hide the nav by default on mobile */
    .nav {
        display: none;
        width: 100%;
    }

    /* When open, show it (which forces the flex-wrap to create a new line) */
    .nav.open {
        display: block;
    }

    .nav ul {
        flex-direction: column;   /* Stack the links vertically */
        margin: 0;
        padding: 0;
    }

    .nav li {
        margin: 10px 0;
        text-align: center;
    }
}
