.intro-container {
    background-color: #444;  /* or a color that fits your design */
    color: #fff;            /* text color */
    padding: 1rem;
    margin-bottom: 1rem;    /* space below the intro box */
    border-radius: 4px;     /* optional rounded corners */
}

.intro-container h2 {
    margin-top: 0;  /* remove default heading top margin if you like */
}

.intro-container p {
    margin-bottom: 0; /* keep paragraphs tight if you prefer */
}
